import React, { useState, useContext, useEffect } from 'react';
import ContactContext from '../../context/contact/contactContext';
import AlertContext from '../../context/alert/AlertContext';
import '../../sass/_contact.scss'

const ContactForm = () => {
    const contactContext = useContext(ContactContext);
    const alertContext = useContext(AlertContext);

    const { addContact, current, clearCurrent, updateContact } = contactContext;
    const { setAlert } = alertContext;

    useEffect(() => {
        if(current !== null) {
            setContact(current);
        } else {
            setContact({
                name: '',
                email: '',
                phone: '',
                type: 'personal'
            });
        }
    }, [contactContext, current]);

    const [contact, setContact] = useState({
        name: '',
        email: '',
        phone: '',
        type: 'personal'
    });

    const { name, email, phone, type } = contact;

    const onChange = e => setContact({ ...contact, [e.target.name]: e.target.value });

    const onSubmit = e => {
        e.preventDefault();

        if (current === null) {
            addContact(contact);
        } else {
            updateContact(contact);
        }
        clearAll();

        if(name === '' && email === '' && phone === '') {
            setAlert('Wypełnij wszystkie pola !', 'danger')
        } else if (name === '') {
            setAlert('Wprowadź imię !', 'danger')
        } 
    }

    const clearAll = () => {
        clearCurrent();
    }

    return ( 
        <div className="form-container">
            <form onSubmit={onSubmit}>
                <h2>{current ? 'Edytuj kontakt' : 'Dodaj Kontakt' }</h2>
                <input 
                    type="text"
                    placeholder="Imię"
                    name="name"
                    value={name}
                    onChange={onChange}
                />
                <input 
                    type="text"
                    placeholder="Imię zwierzęcia"
                    name="email"
                    value={email}
                    onChange={onChange}
                />
                <input 
                    type="text"
                    placeholder="Numer telefonu"
                    name="phone"
                    value={phone}
                    onChange={onChange}
                />
                {/* <h5>Contact Type</h5>
                <input type="radio" onChange={onChange} name="type" value="personal" checked={type === 'personal'}/> Personal {' '}
                <input type="radio" onChange={onChange} name="type" value="professional" checked={type === 'professional'}/> Professional {' '} */}
                <div>
                    <input className="submit" type="submit" value={current ? 'Aktualizuj' : 'Dodaj kontakt' }/> 
                </div>
                {current && (
                    <div>
                        <button className="submit" onClick={clearAll}>Cofnij</button>
                    </div>
                )}
            </form>
        </div>
     )
}
 
export default ContactForm;