import React, { useState, useContext, useEffect } from 'react';
import AlertContext from '../../context/alert/AlertContext';
import AuthContext from '../../context/auth/authContext';
import '../../sass/_auth.scss';

const Register = props => {
    const alertContext = useContext(AlertContext);
    const authContext = useContext(AuthContext);

    const { setAlert } = alertContext;
    const { register, error, clearErrors, isAuthenticated } = authContext;

    useEffect(() => {
        if(isAuthenticated) {
            props.history.push('/');
        }

        if(error === 'User already exist') {
            setAlert(error, 'danger');
            clearErrors();
        }
    }, [error, isAuthenticated, props.history]);

    const [user, setUser] = useState({
        name: '',
        email: '', 
        password: '',
        password2: ''
    });

    const { name, email, password, password2 } = user;

    const onChange = e => setUser({ ...user, [e.target.name]: e.target.value});

    const onSubmit = e => {
        e.preventDefault();
        if(name === '' || email === '' || password === '') {
            setAlert('Please enter all fields', 'danger');
        } else if (password !== password2) {
            setAlert('Passwords do not match', 'danger');
        } else if(password.length < 6) {
            setAlert('Passwords must be 6', 'danger');
        } else {
            register({
                name,
                email,
                password
            });
        }
    };

    return ( 
        <div className="form-container">
            <h1>Zarejestruj się</h1>
            <form onSubmit={onSubmit}>
                <div className="form-group">
                    <label htmlFor="name"></label>
                    <input placeholder='LOGIN' type="text" name='name' value={name} onChange={onChange} />
                </div>
                <div className="form-group">
                    <label htmlFor="email"></label>
                    <input placeholder='ADRES EMAIL' type="email" name='email' value={email} onChange={onChange} />
                </div>
                <div className="form-group">
                    <label htmlFor="password"></label>
                    <input placeholder='HASŁO' type="password" name='password' value={password} onChange={onChange} />
                </div>
                <div className="form-group">
                    <label htmlFor="password2"></label>
                    <input placeholder='POWTÓRZ HASŁO' type="password" name='password2' value={password2} onChange={onChange} />
                </div>
                <input className='submit' type="submit" value='STWÓRZ' />
            </form>
        </div>
     );
}
 
export default Register;