import React, { Fragment, useContext } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import AuthContext from '../context/auth/authContext';
import '@fortawesome/fontawesome-free/css/all.css';
import '../sass/_navbar.scss';

import closeBtn from '../img/closebtn.png';
import menuBtn from '../img/menubtn.png';
import logo from '../img/pawprint.png';

const Navbar = ({ title, icon }) => {
    const authContext = useContext(AuthContext);

    const { isAuthenticated, logout, user } = authContext; 

    const onLogout = () => {
        logout();
    }

    const onClick = () => {
        const ul = document.querySelector('ul');

        ul.classList.toggle('active')
    }
 
    const authLinks = (
        <Fragment>
            {/* <li>Hello { user && user.name } </li> */}
            <li>
                <a onClick={onLogout} href="#!">
                    <i className='fas fa-sign-out-alt'></i>
                    <span>Wyloguj się</span>
                </a>
            </li>
        </Fragment>
    );

    const guestLinks = (
        <Fragment>
            <li>
                <Link to='/register' onClick={onClick}>ZAŁÓŻ KONTO</Link>
            </li>
            <li>
                <Link to='/login' onClick={onClick}>ZALOGUJ SIĘ</Link>
            </li>
        </Fragment>
    )

    return (
        <div className='navbar'>
            <div className="logo-wrapper">
                <img className='logo' src={logo} alt="logo"/> 
                <h1 className='app-title'>{ title }</h1>
            </div>
            <div className='menu-btn btn' onClick={onClick}>
                <img src={menuBtn} alt="menu"/>
            </div>
            <ul>
                <div className='close-btn btn' onClick={onClick} >
                    <img src={closeBtn} alt="close"/>
                </div>

                { isAuthenticated ?  authLinks : guestLinks}
            </ul>
        </div>
    ); 
};

Navbar.propTypes = {
    title: PropTypes.string.isRequired,
    icon: PropTypes.string
}

Navbar.defaultProps = {
    title: 'Groomer Client',
    icon: 'fas fa-paw'
}

export default Navbar


